var AJAX = function AJAX(obj) {
  // 

  /*
  //:obj
  1.deleteAction
  2.type---get post
  3.data--("")
  4.---success
  5. hearders---
  */
  var ajaxObj = null;

  if (window.XMLHttpRequest) {
    var XMLHttpRequest = window.XMLHttpRequest;
    ajaxObj = new XMLHttpRequest();
  } else {
    var ActiveXObject = window.ActiveXObject;
    ajaxObj = new ActiveXObject('Microsoft.XMLHTTP');
  } // 


  ajaxObj.onreadystatechange = function () {
    if (ajaxObj.readyState === 4) {
      if (ajaxObj.status >= 200 && ajaxObj.status < 300 || ajaxObj.status === 304) {
        if (obj.success) {
          obj.success(JSON.parse(ajaxObj.responseText));
        }
      } else {}
    }
  }; // --------------------------------------------


  var params = ''; // 

  if (obj.data) {
    for (var key in obj.data) {
      params += key + '=' + obj.data[key] + '&';
    }

    params = params.slice(0, params.length - 1);
  } // -------------------------------------------
  // type ,


  var type = obj.type || 'get';
  type = type.toLowerCase();

  if (type === 'get') {
    ajaxObj.open(type, obj.deleteAction + '?' + params, true); // 

    if (obj.headers) {
      for (var j in obj.headers) {
        if (j) {
          ajaxObj.setRequestHeader(j, obj.headers[j]);
        }
      }
    }

    ajaxObj.send();
  } else {
    ajaxObj.open(type, obj.deleteAction, true); // 

    if (obj.headers) {
      for (var _j in obj.headers) {
        if (_j) {
          ajaxObj.setRequestHeader(_j, obj.headers[_j]);
        }
      }
    }

    ajaxObj.send(params);
  }
};

export default AJAX;